<script>
export default {
  data(){
    return {
      touch: {
        startX: 0,
        endX: 0
      },
      hover: false,
      hoverRight: false
    }
  },
  methods: {
     mouseMove(e){
      let mouseX = e.clientX;
      let mouseY = e.clientY;

      let img = document.querySelector('.parallax');

      let img_x = mouseX - this.coords(img).x;
      let img_y = mouseY - this.coords(img).y;

      img.style.transform = `translateY(-${img_y/40}px) translateX(-${img_x/40}px) translateZ(100px)`;
    },
    coords (el) {
      let coords = el.getBoundingClientRect();
      return {
        x: coords.left / 2,
        y: coords.top / 2
      }
    },
    goBack() {
      this.$store.commit("setPageTransition", "back");
      this.$router.push({
        name: "Service"
      });
    },
    touchstart(event) {
      this.touch.startX = event.touches[0].clientX;
      this.touch.endX = 0;
    },
    touchmove(event) {
      this.touch.endX = event.touches[0].clientX;
    },
    touchend() {
      if(!this.touch.endX || Math.abs(this.touch.endX - this.touch.startX) < 40)
      return;
      if(this.touch.endX < this.touch.startX){
          this.$store.commit("setPageTransition", "default");
          this.$router.push({
            name: "Contact"
          });
      }
      else{
        this.$store.commit("setPageTransition", "back");
        this.$router.push({
          name: "Service"
        });
      }
    }
  },
  mounted() {
    this.$el.addEventListener('touchstart', event => this.touchstart(event));
    this.$el.addEventListener('touchmove', event => this.touchmove(event));
    this.$el.addEventListener('touchend', () => this.touchend());
  }
}
</script>

<style scoped>
 .work-with-parallax{
    perspective: 1000px;
