    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    height: 100vh;
  }
  .work-with-parallax img {
    height: 100vh;
    width: 100vw;
    z-index: -2;
    object-fit: cover;
    position: absolute;
  }
   
  /* 
  scroll section of the page
   */

  .scroll{
    width: 80%;
    background: #f0f0f3;
    transform: translateY(100vh) translateZ(0);
    padding: 120px;
