  }
  .service-section-left ul li{
    list-style: none;
    font-size: 3rem;
    font-weight: 700;
  }
  .service-section-right{
    width: 50%;
    transition: all 0.3s ease-in-out;
  }
  .service-section-right-content{
    padding-top: 80px;
  }
  .service-section-right-content h3{
    font-size: 1.75rem;
    padding-bottom: 20px;
    font-weight: 500;
  }
  .service-section-right-content p{
    font-size: 1.3rem;
    color: rgb(99, 99, 99);
  }
  .skils{
    text-decoration: none;
    padding-left: 20px;
    margin-bottom: 10px;
  }
  .skils li{
    display: flex;
    margin: 20px 0;
    align-items: center;
  }
  .skils li svg{
    margin-right: 20px;
    width: 50px;
    height: 50px;
  }
  @media (max-width: 1550px) {
    .scroll{
      padding: 60px;
    }
  }
  @media (max-width: 1180px){
    .scroll{
      width: 70%;
